import { useCallback } from 'react';
import { useExpressionModeStore } from '../stores/expressionModeStore';

export interface ExpressionModeState {
  expression: string;
  mode: 'value' | 'expression';
  isEditing: boolean;
  isFullScreen: boolean;
  currentValue?: any;
}

export interface ExpressionModeControls {
  current: 'value' | 'expression';
  switchToValue: () => void;
  switchToExpression: () => void;
  switchToEditor: () => void;
  clear: () => void;
  isExpressionMode: boolean;
  isEditorMode: boolean;
  expression: string;
  setExpression: (expr: string) => void;
  isEditing: boolean;
  setEditing: (editing: boolean) => void;
  currentValue?: any;
  isFullScreen: boolean;
  setFullScreen: (isFullScreen: boolean) => void;
  setCurrentValue: (value: any) => void;

  localInput: string,

  setLocalInput: (value: any) => void;
}

/**
 * Hook for managing expression mode state using Zustand store
 * @param fieldName - Unique identifier for the field
 * @returns Expression mode controls and state
 */
export function useExpressionMode(fieldName: string): Omit<ExpressionModeControls, "localInput" | "setLocalInput"> {
  const store = useExpressionModeStore();
  const state = store.getState(fieldName);

  const switchToValue = useCallback(() => {
    store.switchToValue(fieldName);
  }, [store, fieldName]);

  const switchToExpression = useCallback(() => {
    store.switchToExpression(fieldName);
  }, [store, fieldName]);

  const switchToEditor = useCallback(() => {
    store.switchToEditor(fieldName);
  }, [store, fieldName]);

  const clear = useCallback(() => {
    store.clear(fieldName);
  }, [store, fieldName]);

  const setExpression = useCallback((expr: string) => {
    store.setExpression(fieldName, expr);
  }, [store, fieldName]);

  const setEditing = useCallback((editing: boolean) => {
    store.setEditing(fieldName, editing);
  }, [store, fieldName]);

  const setCurrentValue = useCallback((value: any) => {
    store.setCurrentValue(fieldName, value);
  }, [store, fieldName]);

  const setFullScreen = useCallback((isFullScreen: boolean) => {
    store.setFullScreen(fieldName, isFullScreen);
  }, [store, fieldName]);

  return {
    current: state.mode,
    switchToValue,
    switchToExpression,
    switchToEditor,
    clear,
    isExpressionMode: state.mode === 'expression',
    isEditorMode: state.isEditing,
    expression: state.expression,
    setExpression,
    isEditing: state.isEditing,
    setEditing,
    currentValue: state.currentValue,
    setCurrentValue,
    isFullScreen: state.isFullScreen,
    setFullScreen,
  };
}
