/**
 * Inline Monaco editor for expressions - based on your SingleLineEditor
 */
export function InlineExpressionEditor({
  value,
  onChange,
  onBlur,
  onEnter,
  onTypeInferred,
  onValidationChange,
  context = {},
  className,
  placeholder = 'Enter expression...',
}: InlineExpressionEditorProps) {
  const editorRef = useRef<any>(null);
  const monacoRef = useRef<any>(null);
  const typeInferenceTimeoutRef = useRef<any>(null);

  const handleEditorWillMount = (monaco: any) => {
    monacoRef.current = monaco;

    // Add context variables to completion
    const contextKeys = Object.keys(context);
    if (contextKeys.length > 0) {
      monaco.languages.typescript.javascriptDefaults.addExtraLib(`
        declare const ${contextKeys.join(', ')}: any;
      `, 'context.d.ts');
    }
  };

  const handleEditorDidMount = (editor: any, monaco: any) => {
    editorRef.current = editor;

    // Apply your proven configuration
    editor.updateOptions(InlineEditorOptions);

    console.log(' Inline editor mounted!');

    // Use CSS filter to invert colors for inline editor (light theme effect)
    const editorElement = editor.getDomNode();
    // if (editorElement) {
    //   // editorElement.style = editorElement.style + " background-color: #1e1e1e !important;";
    //   // editorElement.style.filter = 'invert(1) hue-rotate(180deg)';
    // }

    // Disable Find widget
    const findCommand = editor.addCommand(monaco.KeyMod.CtrlCmd | monaco.KeyCode.KEY_F, () => { });

    // Handle Enter and Shift+Enter using onKeyDown
    const enterKeyDisposable = editor.onKeyDown((e: any) => {
      if (e.keyCode === monaco.KeyCode.Enter) {
        // Only handle if THIS editor has focus
        if (!editor.hasTextFocus()) {
          console.log(' Enter keydown but inline editor does NOT have focus - allowing default');
          return;
        }
        
        // Check if Shift is pressed (Shift+Enter)
        if (e.shiftKey) {
          console.log(' Shift+Enter in INLINE editor - switching to full-screen');
          e.preventDefault();
          e.stopPropagation();
          // Switch to full-screen for multi-line editing
          if (onEnter) {
            onEnter();
          }
          return;
        }
        
        // Regular Enter (no Shift)
        // Check if autocomplete is visible
        const suggestWidget = editor._contentWidgets?.['editor.widget.suggestWidget'];
        const isSuggestVisible = 
          suggestWidget?.widget &&
          !suggestWidget.widget._hidden &&
          suggestWidget.widget.state !== undefined &&
          suggestWidget.widget.state !== 0;
        
        const contextKeyService = editor._contextKeyService;
        const suggestWidgetVisible = contextKeyService?.getContextKeyValue?.('suggestWidgetVisible');
        
        if (isSuggestVisible || suggestWidgetVisible) {
          console.log(' Enter in INLINE editor - accepting autocomplete');
          e.preventDefault();
          e.stopPropagation();
        editor.trigger('', 'acceptSelectedSuggestion');
      } else {
          console.log(' Enter in INLINE editor - blurring (finishing edit)');
          e.preventDefault();
          e.stopPropagation();
          // Blur to finish editing
        onBlur?.({});
        }
      }
    });

    // Handle paste - remove line breaks (like your implementation)
    editor.onDidPaste((e: any) => {
      if (e.endLineNumber <= 1) {
        return;
      }
      let newContent = '';
      const textModel = editor.getModel();
      const lineCount = textModel.getLineCount();
      for (let i = 0; i < lineCount; i += 1) {
        newContent += textModel.getLineContent(i + 1);
      }
      textModel.setValue(newContent);
      editor.setPosition({ column: newContent.length + 1, lineNumber: 1 });
    });

    // Handle blur - switch to expression mode when editor loses focus
    editor.onDidBlurEditorText((e: any) => {
      const currentValue = editor.getValue();
      onBlur?.({ ...e, currentValue });
    });

    // Track validation errors on content change
      editor.onDidChangeModelContent(() => {
        const model = editor.getModel();
        if (model) {
        // Validation
        if (onValidationChange) {
          const markers = monaco.editor.getModelMarkers({ resource: model.uri });
          const hasErrors = markers.some((marker: any) => marker.severity === monaco.MarkerSeverity.Error);
          onValidationChange(!hasErrors, markers);
        }
        
        // Debounce type inference - will be cancelled if errors appear via marker change listener
        if (onTypeInferred) {
          if (typeInferenceTimeoutRef.current) {
            clearTimeout(typeInferenceTimeoutRef.current);
          }
          
          console.log(' Content changed - debouncing type inference');
          typeInferenceTimeoutRef.current = setTimeout(async () => {
            const expressionToAnalyze = model.getValue();
            const inferredType = await inferTypeFromMonaco(editor, monaco, expressionToAnalyze, '');
            if (inferredType) {
              onTypeInferred(inferredType);
            }
          }, 200); // Reduced from 500ms to 200ms for better responsiveness
        }
      }
    });
    
    // Listen to MONACO'S marker changes - fires when TypeScript detects errors
    const markerChangeDisposable = monaco.editor.onDidChangeMarkers((uris: any[]) => {
      const model = editor.getModel();
      if (!model || !onTypeInferred) return;
      
      // Check if this marker change is for our model
      const isOurModel = uris.some((uri: any) => uri.toString() === model.uri.toString());
      if (!isOurModel) return;
      
      const markers = monaco.editor.getModelMarkers({ resource: model.uri });
      const hasErrors = markers.some((marker: any) => marker.severity === monaco.MarkerSeverity.Error);
      
      console.log(' Monaco markers changed - hasErrors:', hasErrors);
      
      if (hasErrors) {
        // Cancel any pending type inference and immediately set to error
        if (typeInferenceTimeoutRef.current) {
          clearTimeout(typeInferenceTimeoutRef.current);
          typeInferenceTimeoutRef.current = null;
        }
        console.log(' ERROR detected - immediately setting type to error');
        onTypeInferred('error');
      } else {
        // Errors cleared - trigger type inference
        if (typeInferenceTimeoutRef.current) {
          clearTimeout(typeInferenceTimeoutRef.current);
        }
        console.log(' Errors cleared - triggering type inference');
        typeInferenceTimeoutRef.current = setTimeout(async () => {
          const expressionToAnalyze = model.getValue();
          const inferredType = await inferTypeFromMonaco(editor, monaco, expressionToAnalyze, '');
          if (inferredType) {
            onTypeInferred(inferredType);
          }
        }, 100); // Shorter delay when recovering from error
      }
    });

    // Disable F1 command palette
    editor.addCommand(monaco.KeyCode.F1, () => { });

    // Add immediate type inference on cursor movement (for better responsiveness)
    const cursorDisposable = editor.onDidChangeCursorPosition(async () => {
      if (onTypeInferred && editor.hasTextFocus()) {
        const model = editor.getModel();
        if (model && model.getValue().trim()) {
          // Clear any pending debounced inference
          if (typeInferenceTimeoutRef.current) {
            clearTimeout(typeInferenceTimeoutRef.current);
          }
          
          // Immediate type inference on cursor movement
          const expressionToAnalyze = model.getValue();
          const inferredType = await inferTypeFromMonaco(editor, monaco, expressionToAnalyze, '');
          if (inferredType) {
            onTypeInferred(inferredType);
          }
        }
      }
    });

    // Set cursor to end and focus
    editor.setPosition({
      lineNumber: 1,
      column: 999999,
    });

    window.requestAnimationFrame(() => {
      editor.focus();
      
      // Trigger initial type inference after editor settles
      setTimeout(async () => {
        const model = editor.getModel();
        if (onTypeInferred && model && model.getValue()) {
          console.log(' Triggering initial type inference for inline editor');
          
          // Check for errors first
          const markers = monaco.editor.getModelMarkers({ resource: model.uri });
          const hasErrors = markers.some((marker: any) => marker.severity === monaco.MarkerSeverity.Error);
          
          if (hasErrors) {
            onTypeInferred('error');
          } else {
            const expressionToAnalyze = model.getValue();
            const inferredType = await inferTypeFromMonaco(editor, monaco, expressionToAnalyze, '');
            if (inferredType) {
              onTypeInferred(inferredType);
            }
          }
        }
      }, 100);
    });

    // Store disposables for cleanup
    editorRef.current._inlineDisposables = {
      enterKeyDisposable,
      findCommand,
      markerChangeDisposable,
      cursorDisposable
    };
  };

  // Cleanup when unmounting
  useEffect(() => {
    return () => {
      if (editorRef.current) {
        console.log(' Inline editor unmounting - cleaning up');
        // Clear type inference timeout
        if (typeInferenceTimeoutRef.current) {
          clearTimeout(typeInferenceTimeoutRef.current);
        }
        // Dispose of event listeners
        if (editorRef.current._inlineDisposables) {
          const disposables = editorRef.current._inlineDisposables;
          disposables.enterKeyDisposable?.dispose?.();
          disposables.findCommand?.dispose?.();
          disposables.markerChangeDisposable?.dispose?.();
          disposables.cursorDisposable?.dispose?.();
        }
        editorRef.current = null;
      }
    };
  }, []);

  const handleEditorChange = (newValue: string | undefined) => {
    onChange(newValue || '');
  };

  return (
    <div className={cn("uii:relative uii:flex-1", className)}>
      <Editor
        theme="vs-dark"
        height="26px"
        language="typescript"
        value={value}
        onChange={handleEditorChange}
        onMount={handleEditorDidMount}
        beforeMount={handleEditorWillMount}
        width="100%"
        loading={<><FontAwesomeIcon icon={faSpinner} className="uii:animate-spin" /></>}
        options={InlineEditorOptions}
        className="uii:-ml-[4px] uii:-mt-[1px] inline-editor"
      />
      {!value && (
        <div className="uii:absolute uii:inset-0 uii:flex uii:items-center uii:pointer-events-none uii:text-muted-foreground uii:text-sm">
          {placeholder}
        </div>
      )}
    </div>
  );
}

/**
 * Full panel Monaco editor for expressions
 */
export function FullPanelExpressionEditor({
  value,
  onChange,
  onClose,
  onBlur,
  onTypeInferred,
  context = {},
  className,
  placeholder = 'Enter expression...',
}: FullPanelExpressionEditorProps) {
  const editorRef = useRef<any>(null);
  const monacoRef = useRef<any>(null);
  const typeInferenceTimeoutRef = useRef<any>(null);

  const handleEditorWillMount = (monaco: any) => {
    monacoRef.current = monaco;

    // Add context variables to completion
    const contextKeys = Object.keys(context);
    if (contextKeys.length > 0) {
      monaco.languages.typescript.javascriptDefaults.addExtraLib(`
        declare const ${contextKeys.join(', ')}: any;
      `, 'context.d.ts');
    }
  };

  const handleFPEditorDidMount = (editor: any, monaco: any) => {
    editorRef.current = editor;

    // Apply full panel configuration
    editor.updateOptions(FullPanelEditorOptions);

    console.log(' FULL-SCREEN panel editor mounted!');
    console.log('Full-screen editor is read-only?', editor.getOption(monaco.editor.EditorOption.readOnly));

    // Handle Escape and Enter with onKeyDown
    const escapeKeyDisposable = editor.onKeyDown((e: any) => {
      if (e.keyCode === monaco.KeyCode.Escape) {
        console.log(' Escape pressed in FULL-SCREEN editor - closing');
        e.preventDefault();
        e.stopPropagation();
      onClose();
      }
      
      // Handle Enter key - check for autocomplete first
      if (e.keyCode === monaco.KeyCode.Enter) {
        // Check if suggestion widget is visible
        const suggestWidget = editor._contentWidgets?.['editor.widget.suggestWidget'];
        const isSuggestVisible = 
          suggestWidget?.widget &&
          !suggestWidget.widget._hidden &&
          suggestWidget.widget.state !== undefined &&
          suggestWidget.widget.state !== 0;
        
        const contextKeyService = editor._contextKeyService;
        const suggestWidgetVisible = contextKeyService?.getContextKeyValue?.('suggestWidgetVisible');
        
        if (isSuggestVisible || suggestWidgetVisible) {
          console.log(' Enter in FULL-SCREEN - accepting autocomplete');
          e.preventDefault();
          e.stopPropagation();
          editor.trigger('', 'acceptSelectedSuggestion');
        } else {
          console.log(' Enter in FULL-SCREEN - allowing newline (default behavior)');
          // Don't prevent default - let Monaco insert newline
        }
      }
    });

    // Track content changes
    let changeCount = 0;
    const changeDisposable = editor.onDidChangeModelContent((e: any) => {
      changeCount++;
      const newValue = editor.getValue();
      console.log(` FULL-SCREEN content changed (${changeCount}):`, newValue);
      console.log(` Line count: ${editor.getModel()?.getLineCount()}`);
      
      // Debounce type inference - will be cancelled if errors appear via marker change listener
      if (onTypeInferred) {
        if (typeInferenceTimeoutRef.current) {
          clearTimeout(typeInferenceTimeoutRef.current);
        }
        
        console.log(' Content changed - debouncing type inference');
        typeInferenceTimeoutRef.current = setTimeout(async () => {
          const expressionToAnalyze = newValue;
          const inferredType = await inferTypeFromMonaco(editor, monaco, expressionToAnalyze, '');
          if (inferredType) {
            onTypeInferred(inferredType);
          }
        }, 200); // Reduced from 500ms to 200ms for better responsiveness
      }
    });
    
    // Listen to MONACO'S marker changes - fires when TypeScript detects errors
    const markerChangeDisposable = monaco.editor.onDidChangeMarkers((uris: any[]) => {
      const model = editor.getModel();
      if (!model || !onTypeInferred) return;
      
      // Check if this marker change is for our model
      const isOurModel = uris.some((uri: any) => uri.toString() === model.uri.toString());
      if (!isOurModel) return;
      
      const markers = monaco.editor.getModelMarkers({ resource: model.uri });
      const hasErrors = markers.some((marker: any) => marker.severity === monaco.MarkerSeverity.Error);
      
      console.log(' Monaco markers changed - hasErrors:', hasErrors);
      
      if (hasErrors) {
        // Cancel any pending type inference and immediately set to error
        if (typeInferenceTimeoutRef.current) {
          clearTimeout(typeInferenceTimeoutRef.current);
          typeInferenceTimeoutRef.current = null;
        }
        console.log(' ERROR detected - immediately setting type to error');
        onTypeInferred('error');
      } else {
        // Errors cleared - trigger type inference
        if (typeInferenceTimeoutRef.current) {
          clearTimeout(typeInferenceTimeoutRef.current);
        }
        console.log(' Errors cleared - triggering type inference');
        typeInferenceTimeoutRef.current = setTimeout(async () => {
          const expressionToAnalyze = model.getValue();
          const inferredType = await inferTypeFromMonaco(editor, monaco, expressionToAnalyze, '');
          if (inferredType) {
            onTypeInferred(inferredType);
          }
        }, 100); // Shorter delay when recovering from error
      }
    });

    // Handle blur - switch to expression mode when editor loses focus
    editor.onDidBlurEditorText((e: any) => {
      const currentValue = editor.getValue();
      onBlur?.({ ...e, currentValue });
    });

    // Focus the editor and position cursor at the end
    const model = editor.getModel();
    if (model) {
      const lineCount = model.getLineCount();
      const lastLineLength = model.getLineLength(lineCount);
      editor.setPosition({ lineNumber: lineCount, column: lastLineLength + 1 });
    }
    editor.focus();

    // Trigger initial type inference after a short delay (let editor settle)
    setTimeout(async () => {
      if (onTypeInferred && model) {
        console.log(' Triggering initial type inference for full-screen editor');
        
        // Check for errors first
        const markers = monaco.editor.getModelMarkers({ resource: model.uri });
        const hasErrors = markers.some((marker: any) => marker.severity === monaco.MarkerSeverity.Error);
        
        if (hasErrors) {
          console.log(' Initial load has error - setting type to error');
          onTypeInferred('error');
        } else {
          const expressionToAnalyze = model.getValue();
          const inferredType = await inferTypeFromMonaco(editor, monaco, expressionToAnalyze, '');
          if (inferredType) {
            onTypeInferred(inferredType);
          }
        }
      }
    }, 100);

    // Add immediate type inference on cursor movement (for better responsiveness)
    const cursorDisposable = editor.onDidChangeCursorPosition(async () => {
      if (onTypeInferred && editor.hasTextFocus()) {
        const model = editor.getModel();
        if (model && model.getValue().trim()) {
          // Clear any pending debounced inference
          if (typeInferenceTimeoutRef.current) {
            clearTimeout(typeInferenceTimeoutRef.current);
          }
          
          // Immediate type inference on cursor movement
          const expressionToAnalyze = model.getValue();
          const inferredType = await inferTypeFromMonaco(editor, monaco, expressionToAnalyze, '');
          if (inferredType) {
            onTypeInferred(inferredType);
          }
        }
      }
    });

    // Store disposables for cleanup
    editorRef.current._fullScreenDisposables = {
      escapeKeyDisposable,
      changeDisposable,
      markerChangeDisposable,
      cursorDisposable
    };
  };

  // Cleanup when unmounting
  useEffect(() => {
    return () => {
      if (editorRef.current) {
        console.log(' Full-screen editor unmounting - cleaning up');
        // Clear type inference timeout
        if (typeInferenceTimeoutRef.current) {
          clearTimeout(typeInferenceTimeoutRef.current);
        }
        // Dispose of event listeners
        if (editorRef.current._fullScreenDisposables) {
          const disposables = editorRef.current._fullScreenDisposables;
          disposables.escapeKeyDisposable?.dispose?.();
          disposables.changeDisposable?.dispose?.();
          disposables.markerChangeDisposable?.dispose?.();
          disposables.cursorDisposable?.dispose?.();
        }
        editorRef.current = null;
      }
    };
  }, []);

  const handleEditorChange = (newValue: string | undefined) => {
    onChange(newValue || '');
  };

  return (
    <div
      className={cn(
        "uii:relative uii:border uii:rounded-md uii:bg-background",
        className
      )}
    >
      <div className="uii:flex uii:items-center uii:justify-between uii:p-2 uii:border-b uii:bg-muted/50">
        <span className="uii:text-sm uii:font-medium">Expression Editor</span>
        <button
          type="button"
          onClick={onClose}
          className="uii:text-muted-foreground hover:uii:text-foreground uii:p-1 uii:rounded uii:cursor-pointer uii:text-xs"
        >
          
        </button>
      </div>
      <Editor
        height="300px"
        theme="vs-dark"
        language="typescript"
        value={value}
        onChange={handleEditorChange}
        onMount={handleFPEditorDidMount}
        beforeMount={handleEditorWillMount}
        options={FullPanelEditorOptions}
      />
      {!value && (
        <div className="uii:absolute uii:inset-0 uii:flex uii:items-center uii:justify-center uii:pointer-events-none uii:text-muted-foreground">
          {placeholder}
        </div>
      )}
    </div>
  );
}
