// Types that will come from your API via TRPC
export interface ExpressionContext {
  [key: string]: any;
}

export interface ExpressionResult {
  value: any;
  ast: ExpressionAST;
  error?: string;
  isValid: boolean;
  validation: {
    syntax: boolean;
    expression: boolean;
    context: boolean;
    type?: boolean;
    security?: boolean;
  };
}

export interface ExpressionAST {
  kind: number;
  color?: string;
  left?: ExpressionAST;
  right?: ExpressionAST;
  name?: ExpressionAST;
  argumentExpression?: ExpressionAST;
  arguments?: ExpressionAST[];
  expression?: ExpressionAST;
  escapedText?: string;
  text?: string;
  operator?: string;
  type?: string;
}

export interface ExpressionMetadata {
  expression: string;
  mode: 'value' | 'expression';
  value: any;
  isEditing: boolean;
  isFullScreen: boolean;
}

export interface ExpressionWrapperProps<T = any> {
  /** The label for the field */
  label: string;
  /** The type of the field */
  type: string;
  /** The actual value from the form field */
  value: T | { expression: string; type: 'expression'; value: T };
  /** Unique key for the field (used for debugging/logging) */
  key: string;
  /** Whether the values are loading */
  valuesLoading?: boolean;
  /** Callback when the value changes - second parameter contains expression metadata */
  onValueChange: (
    value: T | { expression: string; type: 'expression' },
    metadata?: ExpressionMetadata
  ) => void;
  /** Callback when dependencies change - called with array of dependency names */
  onDependenciesChange?: (dependencies: string[]) => void;
  /** Current expression metadata (for controlled mode) */
  metadata?: ExpressionMetadata;
  /** Render prop function that receives control props */
  children: (props: {

    value: T;

    onChange: (value: T) => void;

    localInput: T,

    setLocalInput: (value: T) => void;

    expressionMode: ExpressionModeControls;

    onExpressionClick: () => void; // Click handler to trigger expression mode

    inferredType?: string; // TypeScript inferred type of the expression

    hasTypeMismatch?: boolean; // Whether inferred type doesn't match expected type

    hasError?: boolean; // Whether expression has syntax/validation errors

  }) => React.ReactNode;
  /** Unique field name for the field (used for debugging/logging) */
  fieldName: string;
  /** Available nodes for the expression */
  availableNodes?: any;
  /** My interface for the expression */
  myInterface?: any;
  /** Context variables available in expressions */
  expressionContext?: ExpressionContext;
  /** TypeScript type definitions for context variables (for better type inference) */
  contextTypeDefinitions?: string;
  /** Whether to use inline editor (default: true = looks like text input) */
  inlineEditor?: boolean;
  /** Custom className for the wrapper container */
  className?: string;
  /** Custom className for the editor container (when in editor mode) */
  editorClassName?: string;
  /** Custom placeholder for expression editor */
  expressionPlaceholder?: string;
  /** Function to evaluate expressions - can be TRPC mutation, mock, or any other method */
  evaluateExpression?: (
    expression: string,
    context: ExpressionContext
  ) => Promise<ExpressionResult>;
  /** Result of expression evaluation (if parent manages evaluation) */
  expressionResult?: ExpressionResult | null;
  /** Whether expression is currently being evaluated (if parent manages evaluation) */
  isEvaluating?: boolean;
  /** Whether the current expression is valid (for validation states) */
  isExpressionValid?: boolean;
  /** Expected type for the property - used for type coercion validation */
  expectedType?: 'string' | 'number' | 'boolean' | 'array' | 'object' | 'any';
}

/**
 * Safely evaluate JavaScript expressions with context
 * TODO: Add more security hardening, sandboxing, and error handling
 */
function evaluateExpressionLocally(
  expression: string,
  context: ExpressionContext = {}
): any {
  try {
    // Basic validation
    if (!expression || typeof expression !== 'string') {
      return null;
    }

    // TODO: Add expression validation/whitelist for security
    // TODO: Add timeout for long-running expressions
    // TODO: Add memory limits
    // TODO: Add more comprehensive sandboxing

    // Create a safe evaluation context
    const safeContext = {
      ...context,
      // Add common utility functions
      Math,
      Date,
      JSON,
      // Add safe array methods
      Array,
      // Add safe string methods
      String,
      // Add safe number methods
      Number,
      // Add safe boolean methods
      Boolean,
      // Add safe object methods
      Object,
    };

    // Create function with context
    const func = new Function(
      ...Object.keys(safeContext),
      `"use strict"; return (${expression});`
    );

    const result = func(...Object.values(safeContext));

    // TODO: Add result validation and sanitization
    return result;
  } catch (error) {
    console.warn('Expression evaluation failed:', error);
    // TODO: Add better error reporting and user feedback
    return null;
  }
}

// Validate expression locally for different validation classes
function validateExpressionLocally(
  expression: string,
  context: ExpressionContext = {},
  expectedType: string = 'any'
): {
  syntax: boolean;
  expression: boolean;
  context: boolean;
  type: boolean;
  security: boolean;
} {
  const validation = {
    syntax: true,
    expression: true,
    context: true,
    type: true,
    security: true,
  };

  try {
    // Syntax validation - check if it's valid JavaScript
    new Function(expression);
    validation.syntax = true;
  } catch (error) {
    validation.syntax = false;
    return validation; // If syntax is invalid, other validations don't matter
  }

  try {
    // Expression validation - check if it's an expression (not a statement)
    // This is a simple heuristic - expressions typically don't start with keywords
    const trimmed = expression.trim();
    const statementKeywords = [
      'var',
      'let',
      'const',
      'function',
      'class',
      'if',
      'for',
      'while',
      'return',
      'throw',
    ];
    validation.expression = !statementKeywords.some((keyword) =>
      trimmed.startsWith(keyword)
    );
  } catch (error) {
    validation.expression = false;
  }

  try {
    // Context validation - check if referenced variables exist in context
    const contextKeys = Object.keys(context);
    const variableRegex = /\b[a-zA-Z_$][a-zA-Z0-9_$]*\b/g;
    const matches = expression.match(variableRegex) || [];
    const referencedVars = Array.from(new Set(matches));

    // Filter out JavaScript built-ins and keywords
    const jsBuiltins = [
      'true',
      'false',
      'null',
      'undefined',
      'this',
      'new',
      'typeof',
      'instanceof',
    ];
    const userVars = referencedVars.filter((v) => !jsBuiltins.includes(v));

    validation.context = userVars.every((v) => contextKeys.includes(v));
  } catch (error) {
    validation.context = false;
  }

  // Type validation - check if expression result can be coerced to expected type
  try {
    const evaluated = evaluateExpressionLocally(expression, context);
    validation.type =
      evaluated !== null
        ? validateTypeCoercion(evaluated, expectedType)
        : false;
  } catch (error) {
    validation.type = false;
  }

  // Security validation - check for dangerous operations
  const dangerousPatterns = [
    /eval\s*\(/,
    /Function\s*\(/,
    /setTimeout\s*\(/,
    /setInterval\s*\(/,
    /import\s*\(/,
    /require\s*\(/,
  ];
  validation.security = !dangerousPatterns.some((pattern) =>
    pattern.test(expression)
  );

  return validation;
}

// Validate if a value can be coerced to the expected type
function validateTypeCoercion(value: any, expectedType: string): boolean {
  if (expectedType === 'any') return true;

  try {
    switch (expectedType) {
      case 'string':
        // Most values can be coerced to string
        return value !== null && value !== undefined;

      case 'number':
        // Check if value can be converted to a valid number
        if (typeof value === 'number') return !isNaN(value);
        if (typeof value === 'string') {
          const num = Number(value);
          return !isNaN(num) && isFinite(num);
        }
        if (typeof value === 'boolean') return true; // true -> 1, false -> 0
        return false;

      case 'boolean':
        // Most values can be coerced to boolean
        return true; // JavaScript's truthy/falsy conversion

      case 'array':
        // Check if value is already an array or can be converted
        if (Array.isArray(value)) return true;
        if (typeof value === 'string') return true; // String can be split into array
        if (typeof value === 'object' && value !== null) return true; // Object can be Object.entries()
        return false;

      case 'object':
        // Check if value is an object or can be converted
        if (typeof value === 'object' && value !== null) return true;
        if (typeof value === 'string') return true; // String can be parsed as JSON
        if (typeof value === 'number' || typeof value === 'boolean')
          return true; // Can be wrapped in object
        return false;

      default:
        return true;
    }
  } catch (error) {
    return false;
  }
}

/**
 * ExpressionWrapper - Wraps any control with expression capabilities
 *
 * This component provides three modes:
 * 1. Value mode: Shows the wrapped control normally
 * 2. Expression mode: Shows the evaluated expression result
 * 3. Editor mode: Shows the Monaco editor for editing expressions
 */
export function ExpressionWrapper<T = any>({
  label,
  value,
  type,
  valuesLoading = false,
  key,
  onValueChange,
  onDependenciesChange,
  metadata,
  children,
  fieldName,
  expressionContext = {},
  availableNodes,
  myInterface,
  contextTypeDefinitions,
  inlineEditor = true,
  className,
  editorClassName,
  expressionPlaceholder = 'Enter expression...',
  evaluateExpression,
  expressionResult,
  isEvaluating = false,
  isExpressionValid = true,
  expectedType = 'any',
}: ExpressionWrapperProps<T>) {
  // Internal state to track expression metadata (preserves expression when switching modes)
  const [expressionMetadata, setExpressionMetadata] =
    useState<ExpressionMetadata>(
      () => {
        // On initial load, always start in value mode (not editing)
        // Even if metadata says isEditing: true, that's stale state from previous session
        const initialMetadata = metadata || {
          expression: '',
          mode: 'value',
          isEditing: false,
          isFullScreen: false,
          value: undefined,
        };

        // Force isEditing and isFullScreen to false on initial load
        return {
          ...initialMetadata,
          isEditing: false,
          isFullScreen: false,
          mode: 'value', // Always start in value mode
        };
      }
    );

  // Track the original value before any expression was set
  const [originalValue, setOriginalValue] = useState<T>(
    value &&
      typeof value === 'object' &&
      'expression' in value &&
      'type' in value &&
      value.type === 'expression'
      ? value.value
      : (value as T)
  );
  const [currentValue, setCurrentValue] = useState<T>(
    value &&
      typeof value === 'object' &&
      'expression' in value &&
      'type' in value &&
      value.type === 'expression'
      ? value.value
      : (value as T)
  );

  const formatValue = useCallback((value: T | unknown) => {

    switch (type) {
      case "float":
        {
          return (typeof value === "string") && !isNaN(parseFloat(value as string)) ? parseFloat(value as string) : value
        }
      case "integer":
      case "number":
        {
          return (typeof value === "string") && !isNaN(parseInt(value as string)) ? parseInt(value as string) : value
          break
        }
      case "string[]":
        {
          return (typeof value === "string") ? (value as string).split(',') : value as T
          break
        }
      case "string":
      default:
        {
          return value as T
        }
    }
  }, [type])

  const [currentEditorValue, setCurrentEditorValue] = useState<string>();

  const currentExpressionValueExt = useExpressionModeStore
    .getState()
    .getState(fieldName).expression;

  // Update internal metadata when external metadata changes
  useEffect(() => {
    if (metadata) {
      // When receiving external metadata updates, preserve editing state but
      // don't automatically enter editing mode on initial load
      setExpressionMetadata({
        ...metadata,
        // Don't restore editing/fullscreen state from metadata
        // User needs to explicitly click to edit
        isEditing: false,
        isFullScreen: false,
      });
    }
  }, [metadata]);

  // Detect if incoming value is an expression object
  const isExpressionValue = useMemo(() => {
    return (
      value &&
      typeof value === 'object' &&
      'expression' in value &&
      'type' in value &&
      value.type === 'expression'
    );
  }, [value]);

  // Track if this is the initial mount
  const isInitialMount = useRef(true);

  useEffect(() => {


    if (
      value &&
      typeof value === 'object' &&
      'expression' in value &&
      'type' in value &&
      value.type === 'expression'
    ) {
      // Expression object detected
      const exprValue = (value as any).value;
      const exprString = (value as any).expression;

      // Set the current value (the evaluated result to display)
      if (exprValue !== undefined) {
        setCurrentValue(formatValue(exprValue) as T);
        setOriginalValue(formatValue(exprValue) as T);
      }

      // Set the expression in the store AND switch to expression mode (non-editing)
      // This ensures the expression display is shown
      if (exprString && expressionMode.expression !== exprString) {
        expressionMode.setExpression(exprString);
        // Only switch to expression mode on initial load, not during editing
        if (isInitialMount.current && !expressionMode.isEditing) {
          expressionMode.switchToExpression();
        }
      }

      // On initial load, sync metadata to match the expression in the value
      // Check if metadata is out of sync
      const isMetadataOutOfSync =
        !metadata ||
        metadata.expression !== exprString ||
        metadata.mode === 'value' ||  // If value has expression but mode is 'value', it's out of sync
        metadata.isEditing === true ||  // Should never start editing on load
        metadata.isFullScreen === true; // Should never start full-screen on load

      if (isInitialMount.current && isMetadataOutOfSync) {
        const syncedMetadata: ExpressionMetadata = {
          expression: exprString || '',
          value: formatValue(exprValue),
          mode: 'expression', // Show expression display (not editing)
          isEditing: false,
          isFullScreen: false,
        };

        // Use setTimeout to avoid calling onValueChange during render
        setTimeout(() => {
          onValueChange(value, { ...syncedMetadata, value: formatValue(value) });
        }, 0);
      }
    } else {
      // Regular value (not an expression object)
      if (!valuesLoading) {
        setCurrentValue(formatValue(value) as T);
        setOriginalValue(formatValue(value) as T);

        // Sync metadata - clear any expression metadata since we have a plain value
        if (isInitialMount.current && (metadata?.expression || metadata?.mode === 'expression')) {
          const syncedMetadata: ExpressionMetadata = {
            expression: '',
            value: formatValue(value),
            mode: 'value',
            isEditing: false,
            isFullScreen: false,
          };

          // Use setTimeout to avoid calling onValueChange during render
          setTimeout(() => {
            onValueChange(formatValue(value) as T, { ...syncedMetadata, value: formatValue(value) });
          }, 0);
        }
      }
    }

    // Mark that initial mount is complete after a short delay
    if (isInitialMount.current) {
      setTimeout(() => {
        isInitialMount.current = false;
      }, 100);
    }
  }, [value, valuesLoading]);

  // Update metadata when value changes to/from expression
  // useEffect(() => {
  //   if (isExpressionValue) {
  //     // Value is an expression object, switch to expression mode
  //     const newMetadata: ExpressionMetadata = {
  //       expression: (value as any).expression,
  //       mode: 'expression',
  //       isEditing: false
  //     };
  //     setExpressionMetadata(newMetadata);
  //   } else if (expressionMetadata.mode === 'value') {
  //     // This is a regular value and we're in value mode, update original value
  //     setOriginalValue(value);
  //   }
  // }, [value, isExpressionValue, expressionMetadata.mode]);

  // Convert internal metadata to ExpressionModeState format
  // const externalState: ExpressionModeState = {
  //   expression: expressionMetadata.expression,
  //   mode: expressionMetadata.mode,
  //   isEditing: expressionMetadata.isEditing,
  //   isFullScreen: expressionMetadata.isFullScreen,
  //   currentValue: value
  // };

  // // Handle state changes by updating internal metadata and parent
  // const handleStateChange = useCallback((newState: ExpressionModeState) => {

  //   // const newMetadata: ExpressionMetadata = {
  //   //   expression: newState.expression,
  //   //   mode: newState.mode,
  //   //   isEditing: newState.isEditing
  //   // };

  //   // // Update internal state
  //   // setExpressionMetadata(newMetadata);

  //   // // Notify parent
  //   // onValueChange(value, newMetadata);

  //   console.log("handleStateChange called with:", newState);

  // }, [onValueChange, value]);

  const expressionMode = useExpressionMode(fieldName);

  // Parse expression and extract AST + dependencies
  const {
    ast,
    tokens,
    dependencies,
    isLoading: isParsing,
    error: parseError,
  } = useASTParser(expressionMode.expression, fieldName);

  // Memoized dependency list with additional metadata
  const dependencyList = useMemo(() => {
    if (!ast || dependencies.length === 0) {
      return [];
    }

    // Create a structured list of dependencies with metadata
    // The service worker now filters out built-in methods properly
    return dependencies.map((dep) => ({
      name: dep,
      exists: expressionContext ? dep in expressionContext : false,
      value: expressionContext ? expressionContext[dep] : undefined,
    }));
  }, [dependencies, expressionContext, ast]);

  // Store TypeScript type information from Monaco editor
  const [typeScriptInferredType, setTypeScriptInferredType] = useState<string | null>(null);

  // Callback to receive type information from the editor
  const handleTypeInferred = useCallback((type: string) => {
    setTypeScriptInferredType(type);
  }, []);

  // Type inference is now handled directly by the editors (inline and full-screen)
  // No need for the legacy async approach - editors call handleTypeInferred callback

  // Check if expression contains newlines - if so, we should use full-screen editor
  const expressionHasNewlines = useMemo(() => {
    return expressionMode.expression.includes('\n') || expressionMode.expression.includes('\r');
  }, [expressionMode.expression]);

  // Use TypeScript-based type inference from Monaco (always available)
  const inferredReturnType = useMemo(() => {
    // Monaco's TypeScript language service provides accurate type inference
    return typeScriptInferredType || 'unknown';
  }, [typeScriptInferredType]);

  // Check if inferred type matches the expected type prop
  const hasTypeMismatch = useMemo(() => {
    // Don't show type mismatch if there's an error or unknown type
    if (!inferredReturnType || inferredReturnType === 'unknown' || inferredReturnType === 'any' || inferredReturnType === 'error') {
      return false;
    }

    // Normalize types for comparison
    const normalizedInferred = inferredReturnType.toLowerCase().trim();
    const normalizedExpected = (type || expectedType || 'any').toLowerCase().trim();

    // Handle array types: 'string[]' should match 'array'
    if (normalizedExpected === 'array' && normalizedInferred.endsWith('[]')) {
      return false; // Not a mismatch
    }

    // Handle exact matches
    if (normalizedInferred === normalizedExpected) {
      return false; // Not a mismatch
    }

    // If expected type is 'any', never a mismatch
    if (normalizedExpected === 'any') {
      return false;
    }

    // Handle TypeScript literal types - they're compatible with their base type
    // String literals: "adam" is compatible with string
    if (normalizedExpected === 'string' &&
      (normalizedInferred.startsWith('"') || normalizedInferred.startsWith("'"))) {
      return false; // String literal is compatible with string
    }

    // Number literals: 123, 123.45, 10e3, etc. are compatible with number
    if (normalizedExpected === 'number' && /^-?\d+(\.\d+)?([eE][+-]?\d+)?$/.test(normalizedInferred)) {
      return false; // Number literal is compatible with number
    }

    // Integer literals: 123, 10e3, etc. are compatible with integer
    if (normalizedExpected === 'integer' && /^-?\d+(\.\d+)?([eE][+-]?\d+)?$/.test(normalizedInferred)) {
      return false; // Number literal is compatible with integer
    }

    // Number type is compatible with integer (Monaco might infer "number" for integers)
    if (normalizedExpected === 'integer' && normalizedInferred === 'number') {
      return false; // Number is compatible with integer
    }

    // Float literals: 123.45, 10e3, etc. are compatible with float
    if (normalizedExpected === 'float' && /^-?\d+(\.\d+)?([eE][+-]?\d+)?$/.test(normalizedInferred)) {
      return false; // Number literal is compatible with float
    }

    // Number type is compatible with float
    if (normalizedExpected === 'float' && normalizedInferred === 'number') {
      return false; // Number is compatible with float
    }

    // Boolean literals: true/false is compatible with boolean
    if (normalizedExpected === 'boolean' &&
      (normalizedInferred === 'true' || normalizedInferred === 'false')) {
      return false; // Boolean literal is compatible with boolean
    }

    // Otherwise, there's a mismatch
    return true;
  }, [inferredReturnType, type, expectedType]);

  // Internal state for expression evaluation (when wrapper handles it)
  const [internalExpressionResult, setInternalExpressionResult] =
    useState<ExpressionResult | null>(null);
  const [internalIsEvaluating, setInternalIsEvaluating] = useState(false);

  // Hover state for expression preview
  const [isHovering, setIsHovering] = useState(false);

  // Internal Monaco validation state
  const [internalIsExpressionValid, setInternalIsExpressionValid] =
    useState(true);

  // Detailed validation state for different validation classes
  const [validationState, setValidationState] = useState({
    syntax: true,
    expression: true,
    context: true,
    type: true,
    security: true,
  });

  // Check if expression has any errors
  const hasExpressionError = useMemo(() => {
    // No expression = no error
    if (!expressionMode.expression || !expressionMode.expression.trim()) {
      return false;
    }

    // Check Monaco validation errors
    if (!internalIsExpressionValid || !isExpressionValid) {
      return true;
    }

    // Check AST parsing errors
    if (parseError) {
      return true;
    }

    // Check validation state
    if (!validationState.syntax || !validationState.expression) {
      return true;
    }

    // Check if there's an evaluation error
    if (expressionResult?.error || internalExpressionResult?.error) {
      return true;
    }

    return false;
  }, [
    expressionMode.expression,
    internalIsExpressionValid,
    isExpressionValid,
    parseError,
    validationState,
    expressionResult,
    internalExpressionResult
  ]);

  // Track if we should use full editor (when expression becomes multi-line)
  // const [shouldUseFullEditor, setShouldUseFullEditor] = useState(false);

  // Track if we're programmatically clearing to prevent blur handler from firing
  const isProgrammaticallyClearing = useRef(false);

  // Track if we're switching to full-screen to prevent blur handler from firing
  const isSwitchingToFullScreen = useRef(false);

  // Track if we're programmatically clearing to prevent blur handler from firing
  const isBlur = useRef(false);

  // Handle Monaco validation changes
  const handleValidationChange = useCallback(
    (isValid: boolean, errors: any[]) => {
      setInternalIsExpressionValid(isValid);

      // Update syntax validation (Monaco's built-in validation)
      setValidationState((prev) => ({
        ...prev,
        syntax: isValid,
      }));
    },
    []
  );

  // Check if expression content requires full editor
  const checkForMultiLine = useCallback(
    (expression: string) => {
      const hasNewlines = expression.includes('\n');
      const isLong = expression.length > 80; // Arbitrary threshold for "long" expressions
      if (!expressionMode.isFullScreen) {
        expressionMode.setFullScreen(hasNewlines || isLong);
      }
    },
    [expressionMode.isFullScreen]
  );

  // Internal evaluation function
  const internalEvaluateExpression = useCallback(
    async (expression: string, context: ExpressionContext) => {
      if (!evaluateExpression || !expression.trim()) return;

      setInternalIsEvaluating(true);
      try {
        const result = await evaluateExpression(expression, context);
        setInternalExpressionResult(result);
      } catch (error) {
        // console.error('Expression evaluation failed:', error);
        setInternalExpressionResult({
          value: null,
          ast: { kind: 0, text: '', color: '#ff0000' },
          error: error instanceof Error ? error.message : 'Unknown error',
          isValid: false,
          validation: {
            syntax: false,
            expression: false,
            context: false,
            type: false,
            security: false,
          },
        });
      } finally {
        setInternalIsEvaluating(false);
      }
    },
    [evaluateExpression]
  );

  // TODO: Add error boundary for expression evaluation
  // TODO: Add loading states for complex expressions
  // TODO: Add validation for fieldName uniqueness

  // Evaluate expression and determine current value
  // const currentValue = useMemo(() => {

  //   if (expressionMode.isExpressionMode && expressionMode.expression) {
  //     // If we have an expression result (from parent or internal), use it
  //     // const result = expressionResult || internalExpressionResult;
  //     // if (result?.isValid) {
  //     //   return result.value;
  //     // }

  //     // // Use stored currentValue from metadata if available
  //     // if (expressionMode.currentValue !== undefined) {
  //     //   return expressionMode.currentValue;
  //     // }

  //     // Fallback to local evaluation
  //     // try {
  //     //   const evaluated = evaluateExpressionLocally(expressionMode.expression, expressionContext);
  //     //   return evaluated !== null ? evaluated : value;
  //     // } catch (error) {
  //     //   console.warn('Expression evaluation failed:', error);
  //     //   return value;
  //     // }
  //     return currentExpressionValueExt;

  //   }

  //   return originalValue || value;

  // }, [expressionMode.isExpressionMode, expressionMode.expression, expressionMode.currentValue, expressionContext, value, expressionResult, internalExpressionResult]);

  const currentMetadata = useMemo(() => {
    return {
      expression: expressionMode.expression,
      value: currentValue,
      mode: expressionMode.current,
      isEditing: expressionMode.isEditing,
      isFullScreen: expressionMode.isFullScreen,
    };
  }, [
    expressionMode.expression,
    expressionMode.current,
    expressionMode.isFullScreen,
    expressionMode.isEditing,
    currentValue,
  ]);

  const currentExpressionValue = useMemo(() => {
    return { expression: expressionMode.expression, type: 'expression' };
  }, [expressionMode.currentValue || expressionMode.expression]);

  // Handle value changes from wrapped control
  const handleValueChange = useCallback(
    (newValue: T) => {
      // Only pass metadata if we're in expression mode OR if the new value is an expression

      // const currentMetadata: ExpressionMetadata | undefined =

      // (expressionMode.current === 'value' && !isExpressionValue) ? undefined : {
      //     expression: expressionMode.expression,
      //     mode: expressionMode.current,
      //     isEditing: expressionMode.isEditing
      //   };

      // console.log("handleValueChange called with:", { newValue: newValue, currentMetadata: currentMetadata });
      onValueChange(formatValue(newValue) as T, { ...currentMetadata, value: formatValue(newValue) });
    },
    [onValueChange, expressionMode, isExpressionValue, currentValue]
  );

  // // Handle onChange events (for controls that pass events)
  // const handleChange = useCallback((event: any) => {
  //   // const currentMetadata: ExpressionMetadata = {
  //   //   expression: expressionMode.expression,
  //   //   mode: expressionMode.current,
  //   //   isEditing: expressionMode.isEditing
  //   // };

  //   if (event && event.target && typeof event.target.value !== 'undefined') {
  //     // Handle input change events
  //     onValueChange(event.target.value as T, currentMetadata);
  //   } else if (typeof event === 'boolean') {
  //     // Handle switch/checkbox events
  //     onValueChange(event as T, currentMetadata);
  //   } else {
  //     // Handle direct value changes
  //     onValueChange(event as T, currentMetadata);
  //   }
  // }, [onValueChange, expressionMode]);

  // Handle expression changes
  const handleExpressionChange = useCallback(
    (newExpression: string) => {
      // console.log("handleExpressionChange", newExpression);
      // console.log("Current expressionMode.expression before set:", expressionMode.expression);

      // Reset the clearing flag when expression changes (user is typing)
      isProgrammaticallyClearing.current = false;

      expressionMode.setExpression(newExpression);
      setCurrentEditorValue(newExpression);
      // console.log("Current expressionMode.expression after set:", expressionMode.expression);

      // Check if content requires full editor
      checkForMultiLine(newExpression);

      // Update validation state for local validation
      // const localValidation = validateExpressionLocally(newExpression, expressionContext, expectedType);
      // setValidationState(localValidation);

      // // If wrapper handles evaluation internally, trigger it
      // if (evaluateExpression) {
      //   internalEvaluateExpression(newExpression, expressionContext);
      // }

      // console.log("handleExpressionChange calling onValueChange with:", { expression: newExpression, type: 'expression' });

      onValueChange({ expression: newExpression, type: 'expression' } as T, {
        ...currentMetadata,
        expression: newExpression,
      });

      // If in expression mode, update the actual value (fallback to local evaluation)
      // if (expressionMode.isExpressionMode && !evaluateExpression) {
      //   try {
      //     const evaluated = evaluateExpressionLocally(newExpression, expressionContext);
      //     if (evaluated !== null) {
      //       // Store the evaluated value in metadata state
      //       expressionMode.setCurrentValue(evaluated);

      //       const currentMetadata: ExpressionMetadata = {
      //         expression: newExpression,
      //         mode: expressionMode.current,
      //         isEditing: expressionMode.isEditing
      //       };
      //       onValueChange(evaluated, currentMetadata);
      //     }
      //   } catch (error) {
      //     // Keep current value
      //   }
      // }
    },
    [
      expressionMode,
      expressionContext,
      onValueChange,
      evaluateExpression,
      internalEvaluateExpression,
      checkForMultiLine,
    ]
  );

  // Handle expression editor blur - switch to expression mode and update value if needed
  const handleExpressionBlur = useCallback(
    async (e?: any, forceBlur = false) => {

      // If we're programmatically clearing, don't execute blur logic
      if (isProgrammaticallyClearing.current) {
        isProgrammaticallyClearing.current = false;
        return;
      }

      // If we're switching to full-screen, don't execute blur logic
      if (isSwitchingToFullScreen.current) {
        isSwitchingToFullScreen.current = false;
        return;
      }

      // Don't process blur if in full-screen mode (multi-line editing)
      // UNLESS this is a forced blur (like clicking the X button)
      if (expressionMode.isFullScreen && !forceBlur) {
        return;
      }

      // Don't process blur if expression has newlines (should be in full-screen)
      // UNLESS this is a forced blur
      if (expressionHasNewlines && !forceBlur) {
        return;
      }

      isBlur.current = true;

      expressionMode.setEditing(false);
      expressionMode.setFullScreen(false);

      // console.log("handleExpressionBlur called", { currentExpressionValue: currentExpressionValue, currentEditorValue: currentEditorValue, currentExpressionValueExt: currentExpressionValueExt });

      // Reset full editor flag since we're leaving editor mode
    },
    [
      expressionMode,
      expressionResult,
      internalExpressionResult,
      expressionContext,
      onValueChange,
      currentValue,
      isEvaluating,
      internalIsEvaluating,
      currentExpressionValue,
      expressionHasNewlines,
    ]
  );

  useEffect(() => {
    onValueChange(currentValue as T, {
      ...currentMetadata,
      isFullScreen: expressionMode.isFullScreen,
    });
  }, [expressionMode.isFullScreen]);

  // Auto-switch to full-screen mode if newlines are detected while editing
  useEffect(() => {
    if (expressionMode.isEditing && expressionHasNewlines && !expressionMode.isFullScreen) {
      expressionMode.setFullScreen(true);
    }
  }, [expressionHasNewlines, expressionMode.isEditing, expressionMode.isFullScreen]);

  // // Auto-switch to value mode if expression becomes empty after blur
  // useEffect(() => {
  //   // Only check when blur just happened
  //   if (isBlur.current) {
  //     isBlur.current = false; // Reset the flag

  //     // If expression is empty or only whitespace, switch to value mode
  //     if (!expressionMode.expression || !expressionMode.expression.trim()) {
  //       console.log('Expression is empty after blur - switching to value mode');
  //       expressionMode.switchToValue();
  //     }
  //   }
  // }, [expressionMode.isEditing]); // Trigger when isEditing changes (blur sets it to false)

  const handleModeChange = useCallback(
    (newMode: 'value' | 'expression' | 'editor') => {
      switch (newMode) {
        case 'value':
          // When switching to value mode, update the original value to current value
          // setOriginalValue(currentValue);

          expressionMode.switchToValue();

          expressionMode.setFullScreen(false); // Reset full editor flag

          // Update form state with the actual value (not expression object)
          onValueChange(originalValue || value, undefined);
          break;
        case 'editor':
          // setOriginalValue(currentValue);
          expressionMode.switchToEditor();

          // If expression has newlines, automatically go to full-screen
          const shouldBeFullScreen = expressionHasNewlines;
          if (shouldBeFullScreen) {
            expressionMode.setFullScreen(true);
          }

          // Update metadata to reflect editing state
          const editorMetadata: ExpressionMetadata = {
            expression: expressionMode.expression,
            value: currentValue,
            mode: 'expression',
            isEditing: true,
            isFullScreen: shouldBeFullScreen,
          };

          // Notify parent that editing started
          onValueChange(
            expressionMode.expression
              ? { expression: expressionMode.expression, type: 'expression' } as T
              : currentValue as T,
            editorMetadata
          );
          break;
        case 'expression':
          // Store the current value before switching to expression mode
          setOriginalValue(currentValue);

          expressionMode.switchToExpression();

          // If expression has newlines, automatically go to full-screen when editing
          if (expressionHasNewlines) {
            expressionMode.setFullScreen(true);
          }

          // break;
          // expressionMode.switchToEditor();

          break;
      }
    },
    [expressionMode, currentValue, onValueChange, expressionHasNewlines, originalValue, value]
  );

  useEffect(() => {
    // This is for blur detection ... it seems to work quite a bit better than the onBlur event as long
    // as we use the onBlur event to trigger out of editing mode we can watch for that and update values
    // accordinly
    // if (
    //   !expressionMode.isEditing &&
    //   !(expressionMode.expression && expressionMode.expression.trim())
    // ) {
    //   expressionMode.switchToValue();
    // }
    if (isBlur.current) {
      if (!(expressionMode.expression && expressionMode.expression.trim())) {
        expressionMode.switchToValue();
        onValueChange(currentMetadata.value ?? '' as T, {
          ...currentMetadata,
          mode: 'value',
          expression: '',
          isEditing: false,
          isFullScreen: false,
        });
      } else {
        onValueChange(
          currentMetadata.mode === 'value'
            ? (currentMetadata.value ?? '' as T)
            : ({
              expression: currentMetadata.expression.trim(),
              type: 'expression',
            } as T),
          { ...currentMetadata, isEditing: false, isFullScreen: false }
        );
      }
      isBlur.current = false;
    }
  }, [expressionMode.isEditing, expressionMode.isFullScreen, isBlur.current]);

  // Notify parent when dependencies change
  useEffect(() => {
    if (onDependenciesChange) {
      onDependenciesChange(dependencies);
    }
  }, [dependencies, onDependenciesChange]);

  // Handle clear
  const handleClear = useCallback(() => {
    // Set flag to prevent blur handler from executing
    isProgrammaticallyClearing.current = true;

    // const currentMetadata: ExpressionMetadata = {
    //   expression: '',
    //   mode: 'value',
    //   isEditing: false
    // };

    // Return to the original value when clearing the expression
    onValueChange(currentValue as T, currentMetadata);
    handleModeChange('value');
    // expressionMode.clear();
    expressionMode.setFullScreen(false); // Reset full editor flag
  }, [onValueChange, expressionMode, originalValue]);


  const [localInput, setLocalInput] = useState(currentValue);

  useEffect(() => {

    setLocalInput(currentValue)

  }, [currentValue])

  // Create control props for render prop pattern
  const controlProps = useMemo(
    () => ({
      value: currentValue as T,
      onChange: handleValueChange,
      onExpressionClick: () => handleModeChange('editor'), // Click handler to trigger expression mode
      inferredType: inferredReturnType, // Inferred TypeScript type
      hasTypeMismatch: hasTypeMismatch, // Whether inferred type matches expected
      hasError: hasExpressionError, // Whether expression has syntax/validation errors
      localInput,
      setLocalInput,
      expressionMode: {
        current: expressionMode.current,
        switchToValue: () => handleModeChange('value'),
        switchToExpression: () => handleModeChange('expression'),
        switchToEditor: () => handleModeChange('editor'),
        clear: handleClear,
        isExpressionMode: expressionMode.isExpressionMode,
        isEditorMode: expressionMode.isEditorMode,
        expression: expressionMode.expression,
        setExpression: handleExpressionChange,
        isEditing: expressionMode.isEditing,
        setEditing: expressionMode.setEditing,
        isFullScreen: expressionMode.isFullScreen,
        setFullScreen: expressionMode.setFullScreen,
      } as ExpressionModeControls,
    }),
    [
      currentValue,
      handleValueChange,
      expressionMode,
      handleModeChange,
      handleClear,
      handleExpressionChange,
      inferredReturnType,
      hasTypeMismatch,
      hasExpressionError,
    ]
  );

  // Render based on current mode
  const renderContent = () => {
    switch (expressionMode.current) {
      // case 'value':
      //   return children(controlProps);

      case 'expression':
        // Determine what to show based on state
        const hasExpression =
          expressionMode.expression && expressionMode.expression.trim();
        const hasValidAST = (expressionResult || internalExpressionResult)
          ?.isValid;
        const isValidExpression =
          isExpressionValid && internalIsExpressionValid;

        // If no expression is set, automatically switch back to value mode
        // if (!hasExpression) {
        //   console.log('Expression mode render: no expression, switching to value mode', {
        //     expression: expressionMode.expression,
        //     hasExpression,
        //     currentMode: expressionMode.current
        //   });
        //   // Use setTimeout to avoid state update during render
        //   setTimeout(() => {
        //     expressionMode.switchToValue();
        //     // Clear the expression from metadata when switching to value mode due to empty expression
        //     const clearedMetadata: ExpressionMetadata = {
        //       expression: '',
        //       mode: 'value',
        //       isEditing: false
        //     };
        //     onValueChange(value, clearedMetadata);
        //   }, 0);
        //   // Return value mode content while switching
        //   return children(controlProps);
        // }

        return controlProps.expressionMode.isEditing ? (
          <div key={`${key}-expression`} className={cn('relative', className)}>
            {inlineEditor && !expressionMode.isFullScreen && !expressionHasNewlines ? (
              <div
                className={cn(
                  `uii:flex uii:h-9 uii:w-full 
              uii:min-w-0 uii:rounded-sm 
              uii:border uii:border-ring 
              uii:bg-background uii:px-3 uii:py-1 uii:text-base uii:shadow-xs 
              uii:ring-ring/50 uii:ring-[3px] uii:transition-[color,box-shadow] 
              uii:outline-none uii:md:text-sm uii:cursor-pointer
              uii:mt-0.5 `,
                  editorClassName
                )}
              >
                <InlineExpressionEditor
                  value={expressionMode.expression}
                  onChange={(newValue) => {
                    // console.log("InlineExpressionEditor onChange called with:", newValue);
                    handleExpressionChange(newValue);
                  }}
                  onEnter={() => {
                    // Switch to full-screen mode instead of blurring
                    // Set flag to prevent blur handler from closing the editor
                    isSwitchingToFullScreen.current = true;
                    expressionMode.setFullScreen(true);

                    // Reset flag after a short delay in case blur doesn't fire
                    setTimeout(() => {
                      isSwitchingToFullScreen.current = false;
                    }, 100);
                  }}
                  onBlur={handleExpressionBlur}
                  onTypeInferred={handleTypeInferred}
                  onValidationChange={handleValidationChange}
                  context={expressionContext}
                  placeholder={expressionPlaceholder}
                />
                <div className="uii:flex uii:items-center uii:gap-2 uii:mt-0.5">
                  <span
                    className="uii:text-xs uii:font-mono uii:cursor-pointer uii:text-muted-foreground"
                    onMouseDown={(e) => {
                      e.preventDefault(); // Prevent blur from firing
                      e.stopPropagation();
                      isSwitchingToFullScreen.current = true;
                      expressionMode.setFullScreen(true);

                      // Reset flag after a short delay in case blur doesn't fire
                      setTimeout(() => {
                        isSwitchingToFullScreen.current = false;
                      }, 100);
                    }}
                  >
                    <FontAwesomeIcon icon={faExpand} />
                  </span>
                  <span
                    className="uii:text-xs uii:font-mono uii:cursor-pointer uii:text-muted-foreground"
                    onMouseDown={(e) => {
                      e.preventDefault(); // Prevent blur from firing
                      handleClear();
                    }}
                  >
                    Clear
                  </span>
                </div>
              </div>
            ) : (
              <div className="uii:space-y-4">
                <FullPanelExpressionEditor
                  value={expressionMode.expression}
                  onChange={(newValue) => {
                    // console.log("FullPanelExpressionEditor onChange called with:", newValue);
                    handleExpressionChange(newValue);
                  }}
                  onClose={() => {
                    // Treat close like a blur - save the expression
                    // Force blur to bypass full-screen checks
                    handleExpressionBlur(undefined, true);
                    expressionMode.setFullScreen(false);
                  }}
                  onBlur={handleExpressionBlur}
                  onTypeInferred={handleTypeInferred}
                  context={expressionContext}
                  placeholder={expressionPlaceholder}
                />

                {/* AST and Dependencies Display */}
                {(ast || dependencies.length > 0) && (
                  <div className="uii:space-y-3">
                    {/* Return Type */}
                    {inferredReturnType && inferredReturnType !== 'unknown' && (
                      <div className="uii:space-y-2">
                        <div className="uii:flex uii:items-center uii:gap-2">
                          <div className="uii:text-sm uii:font-semibold uii:text-gray-700">
                            Inferred Return Type:
                          </div>
                          {typeScriptInferredType && inferredReturnType !== 'error' && (
                            <span className="uii:px-1.5 uii:py-0.5 uii:text-xs uii:rounded uii:bg-blue-100 uii:text-blue-700 uii:border uii:border-blue-200">
                              TypeScript
                            </span>
                          )}
                        </div>
                        <div className="uii:flex uii:items-center uii:gap-2">
                          {inferredReturnType === 'error' ? (
                            <span className="uii:px-3 uii:py-1.5 uii:text-sm uii:font-mono uii:rounded uii:border uii:bg-red-50 uii:text-red-800 uii:border-red-200">
                               Syntax Error
                            </span>
                          ) : (
                            <>
                              <span className="uii:px-3 uii:py-1.5 uii:text-sm uii:font-mono uii:rounded uii:border uii:bg-purple-50 uii:text-purple-800 uii:border-purple-200">
                                {inferredReturnType}
                              </span>
                              {hasTypeMismatch && (
                                <span className="uii:text-xs uii:text-orange-600 uii:font-semibold">
                                   Expected: {type || expectedType}
                                </span>
                              )}
                            </>
                          )}
                        </div>
                      </div>
                    )}

                    {/* Dependencies */}
                    {dependencyList.length > 0 && (
                      <div className="uii:space-y-2">
                        <div className="uii:text-sm uii:font-semibold uii:text-gray-700">
                          Dependencies ({dependencyList.length}):
                        </div>
                        <div className="uii:flex uii:flex-wrap uii:gap-2">
                          {dependencyList.map((dep, index) => (
                            <span
                              key={index}
                              className={cn(
                                "uii:px-2 uii:py-1 uii:text-sm uii:rounded uii:border",
                                dep.exists
                                  ? "uii:bg-blue-100 uii:text-blue-800 uii:border-blue-200"
                                  : "uii:bg-red-100 uii:text-red-800 uii:border-red-200"
                              )}
                              title={dep.exists ? `Value: ${JSON.stringify(dep.value)}` : 'Not found in context'}
                            >
                              {dep.name}
                            </span>
                          ))}
                        </div>
                      </div>
                    )}

                    {/* AST Tree */}
                    {ast && (
                      <ASTRenderer
                        ast={ast}
                        tokens={tokens}
                        variant="tree"
                        showTokens={true}
                        className="uii:w-full"
                      />
                    )}

                    {/* Parse Status */}
                    {isParsing && (
                      <div className="uii:text-sm uii:text-gray-500 uii:italic">
                        Parsing expression...
                      </div>
                    )}
                    {parseError && (
                      <div className="uii:text-sm uii:text-red-600">
                        Parse Error: {parseError}
                      </div>
                    )}
                  </div>
                )}
              </div>
            )}
          </div>
        ) : (
          <div
            className={cn('uii:relative', className)}
            key={`${key}-expression-display`}
          >
            <div
              className={clsx(
                'uii:flex uii:h-9 uii:w-full uii:min-w-0 uii:rounded-sm uii:border uii:border-input uii:bg-transparent uii:px-3 uii:py-1 uii:text-base uii:shadow-xs uii:transition-[color,box-shadow] uii:outline-none uii:focus-visible:border-ring uii:focus-visible:ring-ring/50 uii:focus-visible:ring-[3px] uii:md:text-sm uii:cursor-pointer uii:hover:bg-muted/50',
                'uii:mt-0.5 uii:bg-white uii:rounded-md uii:items-center'
              )}
              onClick={() => handleModeChange('editor')}
              onMouseEnter={() => setIsHovering(true)}
              onMouseLeave={() => setIsHovering(false)}
            >
              <span className="uii:flex-1 uii:truncate uii:text-foreground uii:items-center">
                {isHovering ? (
                  // Hover state: show AST preview with dependencies
                  <div className="uii:flex uii:items-center uii:gap-2">
                    {ast ? (
                      <ASTRenderer
                        key={`${key}-ast`}
                        ast={ast}
                        tokens={tokens}
                        variant="inline"
                        showTokens={false}
                        className="uii:text-xs"
                      />
                    ) : (
                      <div className="uii:text-xs uii:text-gray-500">
                        {isParsing
                          ? 'Parsing...'
                          : parseError
                            ? `Error: ${parseError}`
                            : 'No AST available'}
                      </div>
                    )}
                    {dependencies.length > 0 && (
                      <div className="uii:flex uii:gap-1">
                        {dependencies.map((dep, index) => (
                          <span
                            key={index}
                            className="uii:px-1 uii:py-0.5 uii:bg-blue-100 uii:text-blue-800 uii:text-xs uii:rounded"
                          >
                            {dep}
                          </span>
                        ))}
                      </div>
                    )}
                  </div>
                ) : (
                  // At rest: show the expression text
                  <span className="uii:font-mono uii:text-sm">
                    {currentExpressionValueExt || 'No expression'}
                  </span>
                )}
              </span>
            </div>
          </div>
        );

      default:
        return children(controlProps);
    }
  };

  return (
    <><div className="uii:flex uii:items-center uii:gap-2 uii:mt-2"><label className="uii:text-xs uii:text-muted-foreground uii:font-bold" htmlFor={key}>{label}:</label>
      <span className="uii:-mt-2 uii:inline-flex uii:px-1 uii:py-0.5 uii:bg-gray-200 uii:rounded-sm uii:text-[10px] uii:font-mono uii:text-muted-foreground uii:font-light">{type}</span>
      {hasTypeMismatch || inferredReturnType === 'error' ? (
        <>
          {expressionMode.current === 'expression' ? <><span className="uii:-mt-2 uii:inline-flex uii:px-1 uii:py-0.5 uii:bg-gray-200 uii:rounded-sm uii:text-[10px] uii:font-mono uii:text-muted-foreground uii:font-light">{expressionMode.expression.trim() !== '' ? inferredReturnType : 'empty'}</span>
            <span className="uii:-mt-2 uii:inline-flex uii:px-1 uii:py-0.5 uii:bg-gray-200 uii:rounded-sm uii:text-[10px] uii:font-mono uii:font-light uii:text-orange-600 ">
               Expected: {type || expectedType}
            </span></> : <></>}</>) : <></>}
    </div>
      {renderContent()}

      {/* {<>availableNodes:{JSON.stringify(availableNodes)}</>}<br />
      {<>myInterface:{JSON.stringify(myInterface)}</>}<br /> */}
      {/* {<>cv:{JSON.stringify(currentValue)}</>}<br />
    {<>ov:{JSON.stringify(originalValue)}</>}<br />
    {<>cevExt:{JSON.stringify(currentExpressionValueExt)}</>}<br />
    {<>cev:{JSON.stringify(currentExpressionValue)}</>}<br />
    {<>exp:{JSON.stringify(expressionMode.expression)}</>}<br />
    {<>ie:{JSON.stringify(expressionMode.isEditing)}</>}<br />
    {<>fs:{JSON.stringify(expressionMode.isFullScreen)}</>}<br />
    {<>expm:{JSON.stringify(expressionMode.isExpressionMode)}</>}<br />
    {<>em:{JSON.stringify(expressionMode.isEditorMode)}</>}<br /> */}
    </>
  );
}

export default ExpressionWrapper;
