import React from 'react';
import { cn } from '@/lib/utils';

interface ASTNode {
  type: string;
  value?: string;
  start: number;
  end: number;
  children: ASTNode[];
  name?: string;
  operator?: string;
  left?: ASTNode;
  right?: ASTNode;
  argument?: ASTNode;
  object?: ASTNode;
  property?: ASTNode;
  index?: ASTNode;
  elements?: ASTNode[];
  params?: ASTNode[];
}

interface ASTRendererProps {
  ast: ASTNode | null;
  key?: string;
  className?: string;
  variant?: 'inline' | 'tree' | 'compact';
  showTokens?: boolean;
  tokens?: Array<{ type: string, value: string, start: number, end: number }>;
}

export function ASTRenderer({
  ast, key,
  className,
  variant = 'inline',
  showTokens = false,
  tokens = []
}: ASTRendererProps) {

  const getNodeColor = (type: string): string => {
    switch (type) {
      case 'StringLiteral':
        return 'uii:text-green-600 uii:bg-green-100';
      case 'NumericLiteral':
        return 'uii:text-purple-600 uii:bg-purple-100';
      case 'Identifier':
        return 'uii:text-blue-600 uii:bg-blue-100';
      case 'Keyword':
      case 'Literal':
        return 'uii:text-orange-600 uii:bg-orange-100';
      case 'BinaryExpression':
        return 'uii:text-gray-700 uii:bg-gray-100';
      case 'CallExpression':
        return 'uii:text-indigo-600 uii:bg-indigo-100';
      case 'MemberExpression':
        return 'uii:text-cyan-600 uii:bg-cyan-100';
      case 'UnaryExpression':
        return 'uii:text-pink-600 uii:bg-pink-100';
      case 'ParenthesizedExpression':
        return 'uii:text-yellow-600 uii:bg-yellow-100';
      default:
        return 'uii:text-gray-600 uii:bg-gray-100';
    }
  };

  const renderInlineNode = (node: ASTNode, depth: number = 0): React.ReactNode => {
    if (depth > 2) return null; // Limit depth for inline display

    // For inline display, show a simplified view
    if (node.type === 'Identifier') {
      return (
        <span key={`${node.start}-${node.end}`} className="uii:inline-flex uii:items-center uii:mr-1">
          <span className="uii:px-1 uii:py-0.5 uii:rounded uii:text-xs uii:bg-blue-100 uii:text-blue-800 uii:font-mono">
            {node.name || node.value}
          </span>
        </span>
      );
    }

    if (node.type === 'StringLiteral' || node.type === 'NumericLiteral') {
      return (
        <span key={`${node.start}-${node.end}`} className="uii:inline-flex uii:items-center uii:mr-1">
          <span className="uii:px-1 uii:py-0.5 uii:rounded uii:text-xs uii:bg-green-100 uii:text-green-800 uii:font-mono">
            {node.value}
          </span>
        </span>
      );
    }

    if (node.type === 'BinaryExpression') {
      return (
        <span key={`${node.start}-${node.end}`} className="uii:inline-flex uii:items-center uii:mr-1">
          {node.left && renderInlineNode(node.left, depth + 1)}
          <span className="uii:px-1 uii:py-0.5 uii:rounded uii:text-xs uii:bg-purple-100 uii:text-purple-800 uii:font-mono uii:mx-1">
            {node.operator}
          </span>
          {node.right && renderInlineNode(node.right, depth + 1)}
        </span>
      );
    }

    if (node.type === 'CallExpression') {
      return (
        <span key={`${node.start}-${node.end}`} className="uii:inline-flex uii:items-center uii:mr-1">
          {(node as any).callee && renderInlineNode((node as any).callee, depth + 1)}
          <span className="text-xs text-gray-600">()</span>
        </span>
      );
    }

    if (node.type === 'PropertyAccessExpression') {
      return (
        <span key={`${node.start}-${node.end}`} className="uii:inline-flex uii:items-center uii:mr-1">
          {node.object && renderInlineNode(node.object, depth + 1)}
          <span className="text-xs text-gray-600">.</span>
          {node.property && renderInlineNode(node.property, depth + 1)}
        </span>
      );
    }

    // For other types, show a simplified version
    return (
      <span key={`${node.start}-${node.end}`} className="uii:inline-flex uii:items-center uii:mr-1">
        <span className="px-1 py-0.5 rounded text-xs bg-gray-100 text-gray-600 font-mono">
          {node.type}
        </span>
      </span>
    );
  };

  const renderTreeNode = (node: ASTNode, depth: number = 0): React.ReactNode => {
    if (depth > 4) return null; // Limit depth to prevent excessive nesting

    const indent = '  '.repeat(depth);

    return (
      <div key={`${node.start}-${node.end}`} className="font-mono text-xs">
        <div className="uii:flex uii:items-start uii:gap-2">
          <span className="uii:text-gray-400">{indent}</span>
          <span className={cn("uii:px-1 uii:py-0.5 uii:rounded uii:text-xs uii:font-semibold", getNodeColor(node.type))}>
            {node.type}
          </span>
          {node.value && (
            <span className="uii:text-green-600">"{node.value}"</span>
          )}
          {node.operator && (
            <span className="uii:text-purple-600">{node.operator}</span>
          )}
          {node.name && (
            <span className="uii:text-blue-600">{node.name}</span>
          )}
        </div>

        {/* Render specific properties more concisely - only if they exist */}
        {/* Don't render children array if we have specific properties to avoid duplication */}
        {node.left && (
          <div className="uii:ml-2">
            <span className="uii:text-gray-500 uii:text-xs">left: </span>
            {renderTreeNode(node.left, depth + 1)}
          </div>
        )}
        {node.right && (
          <div className="uii:ml-2">
            <span className="uii:text-gray-500 uii:text-xs">right: </span>
            {renderTreeNode(node.right, depth + 1)}
          </div>
        )}
        {node.argument && (
          <div className="uii:ml-2">
            <span className="uii:text-gray-500 uii:text-xs">arg: </span>
            {renderTreeNode(node.argument, depth + 1)}
          </div>
        )}
        {node.object && (
          <div className="uii:ml-2">
            <span className="uii:text-gray-500 uii:text-xs">obj: </span>
            {renderTreeNode(node.object, depth + 1)}
          </div>
        )}
        {node.property && (
          <div className="uii:ml-2">
            <span className="uii:text-gray-500 uii:text-xs">prop: </span>
            {renderTreeNode(node.property, depth + 1)}
          </div>
        )}
        {node.index && (
          <div className="uii:ml-2">
            <span className="uii:text-gray-500 uii:text-xs">idx: </span>
            {renderTreeNode(node.index, depth + 1)}
          </div>
        )}

        {/* Only render children array if we don't have specific properties (to avoid duplication) */}
        {!node.left && !node.right && !node.argument && !node.object && !node.property && !node.index &&
          node.children && node.children.length > 0 && (
            <div className="uii:ml-2">
              {node.children.slice(0, 3).map((child, index) => renderTreeNode(child, depth + 1))}
              {node.children.length > 3 && (
                <div className="uii:ml-2 uii:text-gray-500 uii:text-xs">
                  ... and {node.children.length - 3} more
                </div>
              )}
            </div>
          )}
      </div>
    );
  };

  const renderTokens = (): React.ReactNode => {
    if (tokens.length === 0) return null;

    // Show only the first 10 tokens to avoid overwhelming the UI
    const displayTokens = tokens.slice(0, 10);

    return (
      <div className="uii:space-y-2">
        <div className="uii:text-sm uii:font-semibold uii:text-gray-700">
          Tokens {tokens.length > 10 && `(${tokens.length} total)`}:
        </div>
        <div className="uii:flex uii:flex-wrap uii:gap-1">
          {displayTokens.map((token, index) => (
            <span
              key={index}
              className={cn(
                "uii:px-1 uii:py-0.5 uii:rounded uii:text-xs uii:font-mono",
                token.type.includes('Keyword') && "uii:bg-blue-100 uii:text-blue-800",
                token.type.includes('Identifier') && "uii:bg-green-100 uii:text-green-800",
                token.type.includes('String') && "uii:bg-yellow-100 uii:text-yellow-800",
                token.type.includes('Numeric') && "uii:bg-purple-100 uii:text-purple-800",
                token.type.includes('Punctuation') && "uii:bg-gray-100 uii:text-gray-800",
                "uii:bg-gray-100 uii:text-gray-600"
              )}
            >
              {token.value}
            </span>
          ))}
          {tokens.length > 10 && (
            <span className="uii:px-1 uii:py-0.5 uii:rounded uii:text-xs uii:bg-gray-200 uii:text-gray-600">
              +{tokens.length - 10} more
            </span>
          )}
        </div>
      </div>
    );
  };

  if (!ast) {
    return (
      <div className={cn("uii:text-gray-500 uii:text-sm uii:italic", className)}>
        No AST available
      </div>
    );
  }

  if (variant === 'inline') {
    return (
      <div className={cn("uii:inline-flex uii:items-center uii:gap-2", className)}>
        {renderInlineNode(ast)}
      </div>
    );
  }

  if (variant === 'compact') {
    return (
      <div className={cn("uii:space-y-2", className)}>
        <div className="uii:flex uii:flex-wrap uii:gap-1">
          {renderInlineNode(ast)}
        </div>
        {showTokens && renderTokens()}
      </div>
    );
  }

  // Tree variant
  return (
    <div className={cn("uii:space-y-3", className)}>
      <div className="uii:space-y-2">
        <div className="uii:text-sm uii:font-semibold uii:text-gray-700">AST Tree:</div>
        <div className="uii:p-3 uii:border uii:rounded-md uii:bg-gray-50 uii:max-h-64 uii:overflow-auto">
          {renderTreeNode(ast)}
        </div>
      </div>
      {showTokens && renderTokens()}
    </div>
  );
}
