import { useState, useEffect, useRef } from 'react';

interface ASTNode {
  type: string;
  value?: string;
  start: number;
  end: number;
  children: ASTNode[];
  name?: string;
  operator?: string;
  left?: ASTNode;
  right?: ASTNode;
  argument?: ASTNode;
  object?: ASTNode;
  property?: ASTNode;
  index?: ASTNode;
  elements?: ASTNode[];
  params?: ASTNode[];
  raw?: string;
  callee?: ASTNode;
  arguments?: ASTNode[];
  kind?: string;
  text?: string;
}

interface ParseResult {
  ast: ASTNode | null;
  tokens: Array<{type: string, value: string, start: number, end: number}>;
  dependencies: string[];
  error?: string;
}

interface UseASTParserResult {
  ast: ASTNode | null;
  tokens: Array<{type: string, value: string, start: number, end: number}>;
  dependencies: string[];
  isLoading: boolean;
  error: string | null;
}

export function useASTParser(expression: string, controlKey?: string): UseASTParserResult {
  const [ast, setAST] = useState<ASTNode | null>(null);
  const [tokens, setTokens] = useState<Array<{type: string, value: string, start: number, end: number}>>([]);
  const [dependencies, setDependencies] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const requestIdRef = useRef<number>(0);
  const messageHandlerRef = useRef<((event: MessageEvent) => void) | null>(null);
  const controlKeyRef = useRef<string>(controlKey || 'default');
  
  // Update controlKey ref when prop changes
  useEffect(() => {
    controlKeyRef.current = controlKey || 'default';
  }, [controlKey]);

  // Initialize service worker message listener
  useEffect(() => {
    if (typeof window === 'undefined' || !navigator.serviceWorker) {
      setError('Service worker not available');
      return;
    }

    // Create message handler
    const handleMessage = (event: MessageEvent) => {
      if (event.data.type === 'AST_PARSE_RESULT') {
        const { id, controlKey, result } = event.data.payload;
        // Match by both request ID and control key to avoid cross-control contamination
        if (id === requestIdRef.current && controlKey === controlKeyRef.current) {
          console.log('Service worker AST response:', { id, controlKey, result });
          setAST(result.ast);
          setTokens(result.tokens);
          setDependencies(result.dependencies || []);
          setError(result.error || null);
          setIsLoading(false);
        } else {
          console.log('Ignoring AST response - wrong control:', { 
            receivedId: id, 
            expectedId: requestIdRef.current,
            receivedKey: controlKey,
            expectedKey: controlKeyRef.current
          });
        }
      }
    };

    messageHandlerRef.current = handleMessage;
    navigator.serviceWorker.addEventListener('message', handleMessage);

    return () => {
      if (messageHandlerRef.current) {
        navigator.serviceWorker.removeEventListener('message', messageHandlerRef.current);
      }
    };
  }, []);

  // Parse expression when it changes
  useEffect(() => {
    if (!expression.trim()) {
      setAST(null);
      setTokens([]);
      setDependencies([]);
      setError(null);
      setIsLoading(false);
      return;
    }

    if (!navigator.serviceWorker || !navigator.serviceWorker.controller) {
      setError('Service worker not ready');
      setIsLoading(false);
      return;
    }

    setIsLoading(true);
    setError(null);
    requestIdRef.current++;
    
    console.log('Sending expression to service worker:', { 
      expression: expression.trim(), 
      controlKey: controlKeyRef.current,
      id: requestIdRef.current 
    });
    
    // Send message to service worker with control key
    navigator.serviceWorker.controller.postMessage({
      type: 'PARSE_AST',
      payload: {
        expression: expression.trim(),
        id: requestIdRef.current,
        controlKey: controlKeyRef.current
      }
    });
  }, [expression]);

  return {
    ast,
    tokens,
    dependencies,
    isLoading,
    error
  };
}