import { create } from 'zustand';
import { ExpressionModeState } from '../hooks/useExpressionMode';

interface ExpressionModeStore {
  // Store state by field name
  states: Record<string, ExpressionModeState>;

  // Actions
  setState: (fieldName: string, state: ExpressionModeState) => void;
  getState: (fieldName: string) => ExpressionModeState;
  clear: (fieldName: string) => void;

  // Helper actions
  switchToValue: (fieldName: string) => void;
  switchToExpression: (fieldName: string) => void;
  switchToEditor: (fieldName: string) => void;
  setExpression: (fieldName: string, expression: string) => void;
  setEditing: (fieldName: string, isEditing: boolean) => void;
  setCurrentValue: (fieldName: string, value: any) => void;
  setFullScreen: (fieldName: string, isFullScreen: boolean) => void;
}

const defaultState: ExpressionModeState = {
  expression: '',
  mode: 'value',
  isEditing: false,
  currentValue: undefined,
  isFullScreen: false
};

export const useExpressionModeStore = create<ExpressionModeStore>((set, get) => ({
  states: {},

  setState: (fieldName: string, state: ExpressionModeState) => {
    set((store) => ({
      states: {
        ...store.states,
        [fieldName]: state
      }
    }));
  },

  getState: (fieldName: string) => {
    const store = get();
    return store.states[fieldName] || defaultState;
  },

  clear: (fieldName: string) => {
    set((store) => ({
      states: {
        ...store.states,
        [fieldName]: defaultState
      }
    }));
  },

  switchToValue: (fieldName: string) => {
    const currentState = get().getState(fieldName);
    get().setState(fieldName, {
      ...currentState,
      mode: 'value',
      isEditing: false
    });
  },

  switchToExpression: (fieldName: string) => {
    const currentState = get().getState(fieldName);
    get().setState(fieldName, {
      ...currentState,
      mode: 'expression',
      isEditing: false
    });
  },

  switchToEditor: (fieldName: string) => {
    const currentState = get().getState(fieldName);
    get().setState(fieldName, {
      ...currentState,
      mode: 'expression',
      isEditing: true
    });
  },

  setExpression: (fieldName: string, expression: string) => {
    const currentState = get().getState(fieldName);
    get().setState(fieldName, {
      ...currentState,
      expression
    });
  },

  setEditing: (fieldName: string, isEditing: boolean) => {
    const currentState = get().getState(fieldName);
    get().setState(fieldName, {
      ...currentState,
      isEditing
    });
  },
  setFullScreen: (fieldName: string, isFullScreen: boolean) => {
    const currentState = get().getState(fieldName);
    get().setState(fieldName, {
      ...currentState,
      isFullScreen
    });
  },
  setCurrentValue: (fieldName: string, value: any) => {
    const currentState = get().getState(fieldName);
    get().setState(fieldName, {
      ...currentState,
      currentValue: value
    });
  }
}));
